let Vue;//vue

class Store{//new 
    constructor(options){
        this._s = new Vue({//vue
            data:{
                state:options.state
            }
        })
        let innerGetters = options.getters || {};
        this.getters ={};//getters
        Object.keys(innerGetters).forEach((attrName)=>{//
            Object.defineProperty(this.getters,attrName,{
                get:()=>{//thisthis.state
                    return innerGetters[attrName](this.state)//getstate
                }
            })
        })
    }
    get state(){
        return this._s.state;//vuedata
    }
   
}

//vue
const install = (_vuex,attr) =>{//use
    console.log('install',attr);
    Vue = _vuex;
    //state
    //this.$Store

    Vue.mixin({
        beforeCreate() {//
            console.log(this.$options.name,'>>>>>>>>>>');
            //
            //Store
            if(this.$options && this.$options.Store){
                this.$Store = this.$options.Store;
            }else{
                this.$Store = this.$parent && this.$parent.$Store;//new
            }

        },
    })
}

export default{
    install,
    Store//
}
