<template>
  <!-- <transition
    @before-enter="beforeEnter"
    @enter="enter"
    @leave="leave"
    @before-leave="beforeLeave"
    @after-leave="afterLeave"
  > -->
    <div v-show="showMessage" ref="msg" class="message-box wrap" >
      <p class="message">
        <!-- <svg-icon class-name="icon" :icon-class="'m-'+type" /> -->
        <span>{{ message }}</span>
      </p>
      <span v-if="showClose" @click="clickClose">X</span>
    </div>
  <!-- </transition> -->
</template>
 
<script>
export default {
  data() {
    return {
      showMessage: false, // 
      type: '', // info success warning error info
      message: '',
      offset: '30', // top px
      showClose: true, // 
      duration: 3000, //  ms
      onClose: null, // 
      timer: null
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.showMessage = true
      this.close()
    },
    // 
    close() {
      const that = this
      this.timer = setTimeout(() => {
        that.showMessage = false
      }, that.duration)
    },
    // 
    clickClose() {
      this.showMessage = false
      this.timer && clearTimeout(this.timer)
    },
 
    beforeEnter(el) {
      console.log('beforeEnter')
      el.style.opacity = 0
      el.style.top = '0px'
    },
    enter(el) {
      console.log('enter')
      const that = this
      setTimeout(function() {
        el.style.opacity = 1
        el.style.top = that.offset + 'px'
      }, 0)
    },
    beforeLeave(el) {
      console.log('beforeLeave')
      el.style.opacity = 1
      el.style.top = this.offset + 'px'
    },
    leave(el, done) {
      console.log('leave')
      el.style.opacity = 0
      el.style.top = '0px'
      setTimeout(() => {
        done()
      }, 200) // 200ms
    },
    // 
    afterLeave(el) {
      console.log('afterLeave')
      document.body.removeChild(el) // 
      this.onClose && this.onClose()
    }
  }
}
 
</script>
