import Vue from 'vue'
import messagess from './MyMessage.vue'

const MessagePlugin =  Vue.extend(messagess);

export default {
    install(Vue){
        //
        Vue.prototype.$message = function({
            type,
            message,
            duration
        } = {}){
            let nvm ;
            if(!nvm){
                nvm = new MessagePlugin()
                nvm.type = type;//
                nvm.message = message;
                nvm.duration = duration
                nvm.$mount(document.createElement('div')) //div
                document.body.appendChild(nvm.$el) // 
            }
            return nvm.$el;
        }
    }
}