import MyMessage from './MyMessage.vue'
class MyMsg{
    constructor(){
        // this.show(true)
    }
    show(){//:data={}
        let message = new MyMessage({
            el:'#MyMessage',
            data(data) {
                return {
                  showMessage: data, // 
                  type: '', // info success warning error info
                  message: 'hahahahah',
                  offset: '30', // top px
                  showClose: true, // 
                  duration: 3000, //  ms
                  onClose: null, // 
                  timer: null
                }
              },
              mounted() {
                // this.init()
                // this.close()
              },
              methods: {
                init() {
                //   this.showMessage = true
                  this.close()
                },
                // 
                close() {
                  const that = this
                  this.timer = setTimeout(() => {
                    that.showMessage = false
                  }, that.duration)
                },
                // 
                clickClose() {
                  this.showMessage = false
                  this.timer && clearTimeout(this.timer)
                },
             
                beforeEnter(el) {
                  console.log('beforeEnter')
                  el.style.opacity = 0
                  el.style.top = '0px'
                },
                enter(el) {
                  console.log('enter')
                  const that = this
                  setTimeout(function() {
                    el.style.opacity = 1
                    el.style.top = that.offset + 'px'
                  }, 0)
                },
                beforeLeave(el) {
                  console.log('beforeLeave')
                  el.style.opacity = 1
                  el.style.top = this.offset + 'px'
                },
                leave(el, done) {
                  console.log('leave')
                  el.style.opacity = 0
                  el.style.top = '0px'
                  setTimeout(() => {
                    done()
                  }, 200) // 200ms
                },
                // 
                afterLeave(el) {
                  console.log('afterLeave')
                  document.body.removeChild(el) // 
                  this.onClose && this.onClose()
                }
              }
        })
       document.body.appendChild(message.$el)
       return message
    }
}
window.MyMsg = MyMsg
export default MyMsg
